.about__content__btn {
  margin-top: 20px;
  padding: 3px 100px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 40px;
  line-height: 1.42;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3), 0px 10px 20px rgba(0, 0, 0, 0.2),
    0px 4px 6px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #0d7377, #5fa59e);
  transition: all 0.3s ease-in-out;
}
.about__content__btn:active {
  position: relative;
  top: 1px;
  box-shadow: 1px 0px 3px #000000;
  filter: brightness(90%);
}
.about__content__btn:hover {
  background: linear-gradient(135deg, #0d5d5f, #528f96);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.4), 0px 15px 30px rgba(0, 0, 0, 0.3),
    0px 6px 8px rgba(0, 0, 0, 0.3);
}
